// Page header

#front-hero {
		padding: 4em 1em;
	@include breakpoint(large){
		padding: 4em;
	}

	background-color: $yellow;
	.marketing {
		@include grid-row();
	}

	.tagline {
		text-align: center;
		h1 {
			font-size: rem-calc(80);
			line-height: 1em;
			font-weight: 200;
			color: $blue;
			strong {
				color: $purple;
				font-weight: 900;
				text-transform: uppercase;
			}
			small {
				color: $blue;
				font-size: 60%;
			}
			.dance-showcase {
				color: $blue;
			}

			@include breakpoint (small) {
				font-size: rem-calc(50);
			}
			@include breakpoint (medium) {
				font-size: rem-calc(80);
			}
			@include breakpoint (large) {
				font-size: rem-calc(120);
			}
		} /* h1 */
		.subheader {
			color: $secondary-color;
			font-size: rem-calc(20);
			@include breakpoint (large) {
				font-size: rem-calc(35);
			}
		} /* subheader */
	}



}

.section-intro {
	text-align: center;
	h2 {
		font-weight: 700;
		margin-bottom: 1.5rem;
	}

	p {
		font-size: rem-calc(20);
		line-height: 1.6;
	}
}

@include breakpoint(small) {
	h2 {
		font-size: rem-calc(30);
	}
}

@include breakpoint(medium) {
	h2 {
		font-size: rem-calc(50);
	}
}


.cta {
	margin-top: rem-calc(60);
	line-height: 4!important;
}


// Intro

.home .show-intro {
	@include grid-row();
	background: $purple;
	color: $white;
	text-align: center;
	// display: block;
	max-width: 100%;
	margin: 0;
	.mbt-image {
		@include breakpoint(large) {
		    @include grid-column(5);
		}
		display: block;
		height: auto;
		background-size: cover;
		background-position: center;
	}
	.show-intro-contain {
		padding: 4em 1em;
		@include breakpoint(large) {
		    @include grid-column(7);
		    padding: 6em 4em;
		}
		h2 {
			color: $yellow;
			font-weight: 700;
			@include breakpoint(large){
				// font-size: 325%;
			}
		}
		.mbt-image p {
			@include breakpoint(small){
				padding: 0 0 rem-calc(40);
				margin: 0 -15px;
				width: 110%;
				text-align: center;
			}
		}
	}
}

