// Perform
.perform {
	text-align: center;
	background-color: $blue;
	color: $yellow;
	padding: 4em 0;
	.slick-slider {
		height: auto;
	}
	.perform-intro {
		h2 {
			font-weight: 900;
			@include breakpoint(large) {
			    font-size: 4em;
			}
		}
		.button {
			background-color: $purple;
		}
	}
}
.contain {
	// @include grid-row();
	padding: 2em;
}

.benefits {
	background-color: $blue;
	color: $white;
	text-align: center;
	@include grid-row();
	max-width: 100%;
	margin: 0;
	display: table; 
	a {
		color: $yellow;
		border-color: lighten($blue, 10%);
		// min-height: 600px;
	}
	.button {
		background-color: $yellow;
		color: $blue;
		border-color: lighten($blue, 10%);
	}
	.benefits-bg {
		@include grid-column(12);
		@include breakpoint(large) {
		    @include grid-column(4);
		}
		display: block;
		display: table-cell; vertical-align: middle;
		background-size: cover;
		padding: 4em;
	}
	.benefits-intro {
		display: table-cell; 
		vertical-align: middle;
		padding: 4em 1em;
		@include grid-column(12);
		@include breakpoint(large) {
		    @include grid-column(8);
		    // text-align: left;
		    padding: 6em 4em;
		    h2 {
		    	font-size: 3em;
		    }
		}
	}
}

// Raffle
.raffle-donors {
	text-align: center;
	padding: 4em 0;
	header {
		margin-bottom: 1em;
		padding: 2em 0;
	}
	ul {
		list-style: none;
		li {
			display: inline;
			font-size: 1.5em;
			font-weight: 700;
			padding: .75em;
			&:nth-child(2n) {
				a {
					color: darken($blue, 10%);
				}
			}
		}
	}
}

// Section divider
.section-divider {
	@include grid-row();
	hr {
		@include grid-column(12);
		@extend .dotted;
	}
}

